@import "icon-varibles.scss";
 
@font-face {
  font-family: '#{$icomoon-font-family}';
  src:
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?pr4uck') format('truetype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?pr4uck') format('woff'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?pr4uck##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
 
[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
  speak: never; 
  font-style: normal;     
  font-weight: normal;  
  font-variant: normal; 
  text-transform: none;
  line-height: 1;
 
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

